import useMediaQuery from '@mui/material/useMediaQuery';
import { Link } from 'react-router-dom';
import Button from '@mui/material/Button';
import { Branding } from '../brand';
import { TopBarGridContainer } from './utils';
import BurgerMenu from './BurgerMenu';
import ProfileLink from '../profile';

function BrandLink() {
  const belowsm = useMediaQuery((theme) => theme.breakpoints.down('sm'));
  const branding = belowsm ? <Branding width="55px" height="55px" fontSize="1.55rem" /> : <Branding />;
  return (
    <Link style={{ textDecoration: 'none' }} to="/memories">
      <Button>
        {branding}
      </Button>
    </Link>
  );
}

export default function TopBar() {
  const belowmd = useMediaQuery((theme) => theme.breakpoints.down('md'));
  const belowsm = useMediaQuery((theme) => theme.breakpoints.down('sm'));

  if (belowsm) {
    return (
      <TopBarGridContainer>
        <BrandLink />
      </TopBarGridContainer>
    );
  }

  if (belowmd) {
    return (
      <TopBarGridContainer>
        <BurgerMenu />
        <BrandLink />
        <ProfileLink />
      </TopBarGridContainer>
    );
  }

  return (
    <TopBarGridContainer>
      <BrandLink />
      <ProfileLink />
    </TopBarGridContainer>
  );
}
