import PropTypes from 'prop-types';
import { Link, useNavigate } from 'react-router-dom';
import Box from '@mui/material/Box';
import Stack from '@mui/material/Stack';
import IconButton from '@mui/material/IconButton';
import useMediaQuery from '@mui/material/useMediaQuery';
import HomeIcon from '@mui/icons-material/Home';
import AddOutlinedIcon from '@mui/icons-material/AddOutlined';

import TopBar from '../components/topbar';
import Footer from '../components/footer';
import BurgerMenu from '../components/topbar/BurgerMenu';
import ProfileLink from '../components/profile';

function DefaultBackground({ children }) {
  return (
    <Box
      sx={{
        backgroundColor: 'basepage.background',
        overflow: 'auto',
        flex: 1,
        width: 1,
      }}
    >
      {children}
    </Box>
  );
}

function HomeButton() {
  return (
    <Link style={{ textDecoration: 'none' }} to="/memories">
      <IconButton sx={{ paddingTop: '0.3rem' }}>
        <HomeIcon fontSize="medium" sx={{ width: 45, height: 45, color: 'button.main' }} />
      </IconButton>
    </Link>
  );
}

function CreateMemoryIcon() {
  const navigate = useNavigate();
  return (
    <AddOutlinedIcon
      onClick={() => navigate('/memory/create')}
      sx={{
        border: 2,
        borderRadius: '50%',
        fontSize: '2rem',
        color: 'button.main',
      }}
    />
  );
}

function ProfileBar({ create }) {
  return (
    <Stack
      direction="row"
      justifyContent="space-between"
      alignItems="center"
      sx={{
        borderTop: 4,
        borderColor: 'secondary.main',
        backgroundColor: 'primary.main',
      }}
    >
      <BurgerMenu />
      {create ? <CreateMemoryIcon /> : <HomeButton />}
      <ProfileLink />
    </Stack>
  );
}

export default function BasePage({ create, children }) {
  const belowsm = useMediaQuery((theme) => theme.breakpoints.down('sm'));
  return belowsm ? (
    <Box
      sx={{
        display: 'flex',
        flexDirection: 'column',
        flexWrap: 'no-wrap',
        height: '100vh',
        width: '100vw',
      }}
    >
      <DefaultBackground>{children}</DefaultBackground>
      <ProfileBar create={create} />
    </Box>
  ) : (
    <Box
      sx={{
        display: 'flex',
        flexDirection: 'column',
        flexWrap: 'no-wrap',
        height: '100vh',
        width: '100vw',
      }}
    >
      <TopBar />
      <DefaultBackground>{children}</DefaultBackground>
      <Footer />
    </Box>
  );
}

DefaultBackground.propTypes = {
  children: PropTypes.node.isRequired,
};

BasePage.defaultProps = {
  create: false,
};

BasePage.propTypes = {
  children: PropTypes.node.isRequired,
  create: PropTypes.bool,
};

ProfileBar.defaultProps = {
  create: false,
};

ProfileBar.propTypes = {
  create: PropTypes.bool,
};
