import * as React from 'react';
import PropTypes from 'prop-types';
import Grid from '@mui/material/Grid';
import Dialog from '@mui/material/Dialog';
import IconButton from '@mui/material/IconButton';
import CloseIcon from '@mui/icons-material/Close';
import DialogTitle from '@mui/material/DialogTitle';
import DialogContent from '@mui/material/DialogContent';

export function TopBarGridContainer({ children }) {
  return (
    <Grid
      container
      alignItems="center"
      sx={{
        p: 0,
        pb: 1,
        borderBottom: 4,
        borderColor: 'secondary.main',
        flexShrink: 0,
        backgroundColor: 'primary.main',
        justifyContent: {
          sm: 'space-between',
          xs: 'space-between',
        },
      }}
    >
      {children}
    </Grid>
  );
}

function CloseButton({ onClick }) {
  return (
    <IconButton
      edge="start"
      onClick={onClick}
      aria-label="close"
      sx={{
        fontSize: '3.5rem',
        position: 'absolute',
        top: 0,
        right: 0,
      }}
    >
      <CloseIcon sx={{ fontSize: '3rem', fill: 'muted.main' }} />
    </IconButton>
  );
}

export function TopBarDialog({
  text, open, onClose, onClick, children, wdth,
}) {
  return (
    <Dialog open={open} onClose={onClose} fullWidth maxWidth={wdth}>
      <DialogTitle sx={{ backgroundColor: 'transparent' }}>
        {text}
        <CloseButton onClick={onClick} />
      </DialogTitle>
      <DialogContent dividers sx={{ p: 0 }}>{children}</DialogContent>
    </Dialog>
  );
}

TopBarDialog.propTypes = {
  text: PropTypes.string.isRequired,
  open: PropTypes.bool.isRequired,
  children: PropTypes.node.isRequired,
  wdth: PropTypes.string.isRequired,
  onClose: PropTypes.func.isRequired,
  onClick: PropTypes.func.isRequired,
};

TopBarGridContainer.propTypes = {
  children: PropTypes.node.isRequired,
};

CloseButton.propTypes = {
  onClick: PropTypes.func.isRequired,
};
